.home-sweet-home {
  margin-top: 100px;
  margin-bottom: 195px;
  .container-narrow {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
  }
  .left {
    width: 47%;
    position: relative;
    .top-left {
      animation: orbit 6s linear infinite;
      transform-origin: 53% center;
      top: 15%;
      left: -6%;
      width: 28%;
      &::after {
        padding-top: 95%;
        background-color: $blue;
        animation: orbit 6s linear reverse infinite;
        border-radius: 70% 30% 30% 70% / 40% 60% 40% 60%;
      }
    }
    .top-right {
      animation: orbit 5s linear reverse infinite;
      transform-origin: 54% center;
      top: 15%;
      right: 7%;
      width: 17%;
      &::after {
        padding-top: 85%;
        background-color: $red;
        animation: orbit 5s linear infinite;
        border-radius: 54% 46% 70% 30% / 56% 30% 70% 44%;
      }
    }
    .bottom {
      animation: orbit 7s linear infinite;
      transform-origin: 52% center;
      bottom: -5%;
      left: 3%;
      width: 40%;
      &::after {
        padding-top: 95%;
        background-color: $red;
        animation: orbit 7s linear reverse infinite;
        border-radius: 70% 30% 30% 70% / 40% 60% 40% 60%;
      }
    }
    .bubble {
      position: absolute;
      &::after {
        content: '';
        display: block;
        position: relative;
      }
    }
    img { width: 100%; }
  }
  .right {
    margin-bottom: 22px;
    width: 40%;
  }
  .title {
    margin-bottom: 14px;
    font-size: 40px;
    color: $red;
  }
  .text {
    margin-bottom: 27px;
    width: 100%;
    max-width: 375px; // really unnecessary because math
    font-size: 18px;
  }
  .social {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    a {
      height: 43px;
      width: 43px;
      margin-right: 14px;
      text-decoration: none;
      border-radius: 50%;
      background-color: $blue;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      &:last-of-type {
        margin-right: unset;
      }
      img {
        width: 60%;
        height: auto;
      }
    }
  }
}
.the-stacks {
  margin-bottom: 154px;
  .container-wide {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: stretch;
    border: solid 1px $border-color;
    border-radius: 35px;
  }
  .role {
    width: 25%;
    padding: 30px 0 80px;
    border-right: solid 1px $border-color;
    margin-right: -1px;
    &:last-of-type {
      border-right: unset;
      margin-right: unset;
    }
  }
  .illy {
    display: block;
    width: 55%;
    object-fit: contain;
    object-position: top center;
    margin: 0 auto 10px;
    &::after {
      content: '';
      display: inline-block;
      position: relative;
      padding-top: 100%
    }
  }
  .rando-bg-fill { fill: $white; }
  .name {
    text-align: center;
    font-size: 29px;
    color: $red;
    margin-bottom: 30px;
  }
  .blurb {
    width: 80%;
    max-width: 200px;
    margin: 0 auto;
    text-align: center;
    font-weight: 400;
  }
}



@media (max-width: 951px) {
  .home-sweet-home {
    margin-bottom: 100px;
    margin-top: 80px;
    .container-narrow {
      align-items: center;
    }
    .right {
      width: 47%;
    }
  }
  .the-stacks {
    .container-wide {
      width: 80%;
    }
    .role {
      width: 50%;
      padding: 50px 0 40px;
      &:nth-child(1), &:nth-child(2) {
        border-bottom: solid $border-color 1px;
      }
      &:nth-child(2n) {
        margin-right: unset;
        border-right: unset;
      }
    }
  }
}

@media (max-width: 651px) {
  .home-sweet-home {
    margin-top: 20px;
    .container-narrow {
      flex-direction: column;
    }
    .left, .right {
      width: 100%;
      max-width: 375px;
      margin: 0 auto;
    }
  }
  .the-stacks {
    .container-wide {
      width: 90%;
    }
  }
}
